<template>
    <div class="query-box">
      <!-- 搜索固定标题行 -->
      <div class="query-box-title" @click="show = ! show"  ref="queryBox">
        <span class="search-title" v-show="queryData.length===0">查询条件</span>
        <div search-items>
          <span class="queryTerm" v-for="(item, index) in queryData" v-bind:key="index">
            <strong>{{item.name}}</strong>
            <span>
              {{item.text}}
            </span>
          </span>
        </div>
        <div style="text-align: right">
          <Button type="primary" size="small" icon="ios-arrow-dropdown">查询条件</Button>
          <slot></slot>
        </div>
      </div>

      <!-- 搜索查询条件下拉框 -->
      <div class="query-down" v-show="show"  ref="queryDown">
        <div class="queryTerm">
          <TextTerm titName="模板名称" titKey="realName" @updataQueryData ="updataQueryData" @delQueryTrem="delQueryTrem"></TextTerm>
        </div>
        <div style="text-align: right;margin-top:-45px;">
          <Button type="primary" size="small" icon="ios-search" style="margin-right: 10px" @click="search">搜索</Button>
          <Button type="primary" size="small" icon="md-close" @click="show = false">取消</Button>
        </div>
      </div>
    </div>
</template>

<script>
import CascadeTerm from '@/components/CascadeTerm'
import TextTerm from '@/components/TextTerm'
export default {
  name: 'queryBox',
  data () {
    return {
      show: false,
      /* 单位名称 */
      UnitName: '',
      queryData: []
    }
  },
  components: {
    CascadeTerm,
    TextTerm
  },
  mounted () {
    document.addEventListener('click', this.queryHide)
  },
  beforeDestroy () {
    document.removeEventListener('click', this.queryHide)
  },
  methods: {
    /* queryData中对象的添加和删除
    * name 添加字段文本
    * text 添加字段值得文本
    * key 字段名
    * value 字段值 */
    updataQueryData (obj) {
      if (obj.text !== '') {
        this.queryData = this.queryData.filter((e) => e.name !== obj.name)
        this.queryData.push(obj)
      } else {
        this.queryData = this.queryData.filter((e) => e.name !== obj.name)
      }
    },
    /* 删除已选中查询条件 */
    delQueryTrem (key) {
      /* 删除对应字段的查询数据 */
      this.queryData = this.queryData.filter((obj) => obj.key !== key)
    },
    /* 搜索 */
    search () {
      console.log(this.queryData)
      let obj = {}
      for (let arr of this.queryData) {
        obj[arr.key] = arr.value
      }
      console.log(obj)
      this.$emit('searchSrldId', obj)
      this.show = false
    },
    /* 点击任意地方隐藏搜索栏 */
    queryHide (e) {
      if ((!this.$refs.queryBox.contains(e.target)) && (!this.$refs.queryDown.contains(e.target))) {
        this.show = false
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .query-box{
    height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    .query-box-title{
      line-height: 50px;
      font-size: 14px;
      overflow: hidden;
      padding: 0 0 10px;
      display: flex;
      justify-content: space-between;
      .queryTerm{
        margin-left: 10px;
        strong{
          margin-right: 10px;
          font-weight: normal;
        }
        span{
          padding:2px 10px;
          background: #61A7F3;
          border-radius: 4px;
          color:#fff;
          margin-left: 5px;
          margin-right: 25px;
          position: relative;
        }
      }
    }
    .search-title {
      &:before {
        content: '';
        display: inline-block;
        width: 18px;
        height: 18px;
        background: url("../../assets/redLine/sprite.png") 0 -63px no-repeat;
        background-size: auto;
        vertical-align: middle;
        margin-right: 8px;
      }
    }
    .search-items {
      flex-grow: 1;
      padding: 0 5px;
      text-align: left
    }
    .query-down{
      position: absolute;
      top: 50px;
      left: 5px;
      right: 5px;
      background: white;
      box-shadow: 3px 3px 5px rgba(0,0,0,.2);
      border: 1px solid #f5f5f5;
      border-radius: 6px;
      z-index: 99999999;
      padding: 10px 20px 35px;
      .queryTerm{
        padding:10px 5px;
        text-align: left;
        .textTerm {
          span {
            width: 120px !important;
          }
        }
      }
    }
  }
  .blue-block{
    display: inline-block;
    background: #61a7f3;
    color: white;
    padding: 0 10px;
    border-radius: 3px;
    margin-right: 15px;
  }
</style>
