<template>
  <div id="userManage">
    <QueryBox @searchSrldId="searchSrldId">
      <Button type="success" size="small" icon="md-add" class="btn-add" style="margin-right: 10px" @click.stop="openAdd">新建</Button>
    </QueryBox>
    <div class="tab_box">
      <Table ref="table" border stripe :loading="loading" :columns="columns1" :data="data1"  size="small" highlight-row :max-height="tableHeight"></Table>
    </div>
    <div class="page_box" id="pageBox">
      <Page :total="total" :page-size="pageSize" show-elevator @on-change="pageChange" size="small" show-total/>
    </div>
    <UserAdd ref="userAdd" :codeData="codeData" :departmentList="departmentList" @getTableData="getTableData"></UserAdd>
    <UserEdit ref="userEdit" :data="editDefaultData" :codeData="codeData" :departmentList="departmentList" @getTableData="getTableData"></UserEdit>
    <Modal v-model="modal1"
           :mask-closable="true"
           title="新建台账组">
      <Form :model="formItem" :label-width="80">
        <FormItem label="台账组名称">
          <Input v-model="formItem.name" placeholder="请输入台账组名称"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" :loading="loading" @click="addSubmit">确定</Button>
      </div>
    </Modal>
    <Modal v-model="modal2"
           :mask-closable="true"
           title="编辑台账组">
      <Form :model="formItem1" :label-width="80">
        <FormItem label="台账组名称">
          <Input v-model="formItem1.name" placeholder="请输入台账组名称"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" :loading="loading1" @click="submitUpdate">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { taizhangdel, taizhangadd, taizhangupdate, taizhanglist } from '@/api/systemManage'
import UserAdd from './User_add'
import UserEdit from './User_edit'
import QueryBox from './template_query'
export default {
  name: 'taizhangzu',
  data () {
    return {
      /* 查询条件 */
      code: '',
      limit: 10,
      realName: '',
      phoneNumber: '',
      department: '',
      /* 表格动态高度 */
      tableHeight: window.innerHeight - 290,
      /* 每页条数 */
      pageSize: Math.floor((window.innerHeight - 290) / 40) - 1,
      /* 页码 */
      pageNum: 1,
      /* 页面总条数 */
      total: 0,
      /* 重置密码数据id */
      resId: '',
      /* 删除数据id */
      delId: '',
      /* 编辑框默认原数据 */
      editDefaultData: {},
      /* 表格 */
      columns1: [
        {
          title: '序号',
          align: 'center',
          width: 100,
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '台账组名称',
          key: 'name'
        },
        {
          title: '操作',
          key: 'action',
          width: 270,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.modal2 = true
                    this.formItem1.name = params.row.name
                    this.formItem1.id = params.row.id
                    this.formItem1.remark = params.row.remark
                  }
                }
              }, '编辑'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.delId = params.row.id
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该台账组？',
                      onOk: () => {
                        this.sysDel()
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data1: [],
      /* 行政区划数据 */
      codeData: [],
      /* 部门列表 */
      departmentList: [],
      /* 加载 */
      loading: false,
      /* 加载 */
      loading1: false,
      // 控制模态框的展示
      modal1: false,
      // 添加表格数据
      formItem: {
        name: ''
      },
      // 修改表格数据
      formItem1: {
        name: '',
        id: 0
      },
      // 控制模态框的展示
      modal2: false,
      type: 1 // 类型
    }
  },
  components: {
    UserAdd,
    UserEdit,
    QueryBox
  },
  mounted () {
    /* 监听浏览器高度 */
    window.onresize = function () {
      this.tableHeight = window.innerHeight - 290
      this.pageSize = Math.floor((window.innerHeight - 290) / 40) - 1
    }.bind(this)
    this.getTableData()
  },
  methods: {
    /* axios: 获取列表数据 */
    getTableData () {
      this.loading = true
      taizhanglist({
        data: {
          pageIndex: this.pageSize * (this.pageNum - 1) + 1,
          pageSize: this.pageSize,
          name: this.realName,
          type: this.type
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.poList
          this.total = res.data.data.poSum
          this.loading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    openAdd () {
      this.modal1 = true
      this.formItem.name = ''
    },
    // 提交上传
    addSubmit () {
      this.loading = true
      taizhangadd({
        data: {
          name: this.formItem.name
        }
      }).then(res => {
        this.loading = false
        this.modal1 = false
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '创建台账组成功' })
          this.getTableData()
        } else {
          this.$Notice.error({ title: '创建台账组失败', desc: res.data.msg })
        }
      })
    },
    /* axios: 删除数据 */
    sysDel () {
      taizhangdel({
        data: {
          id: this.delId
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableData()
          this.$Notice.success({ title: '删除成功' })
        } else {
          this.$Notice.error({ title: '删除失败' })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    // 打开修改
    openUpdate () {
      this.modal2 = true
      this.formItem1.name = ''
    },
    // 修改提交
    submitUpdate () {
      this.loading1 = true
      taizhangupdate({
        data: {
          name: this.formItem1.name,
          id: this.formItem1.id
        }
      }).then(res => {
        this.loading1 = false
        this.modal2 = false
        if (res.data.code === '0000') {
          this.getTableData()
          this.$Notice.success({ title: '编辑成功' })
        } else {
          this.$Notice.error({ title: '编辑失败', desc: res.data.msg })
        }
      })
    },
    /* 查询框 */
    searchSrldId (obj) {
      this.realName = obj.realName
      this.getTableData()
    },
    /* 分页跳转 */
    pageChange (p) {
      this.page = p
    }
  }
}
</script>

<style scoped lang="scss">
#userManage{
  .from_box{
    padding:10px;
    text-align: left;
  }
  .tab_box{
    padding: 0 15px;
  }
  .page_box{
    width: 100%;
    padding: 15px;
    text-align: right
  }
  .btn-add {
    background-color: #01c296;
    border-color: #01c296;
    margin-left: 10px;
    &:hover, &:focus {
      background-color: #05ab85;
      border-color: #05ab85;
    }
  }
}
</style>
